#container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

#carNumberWrapper {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    column-gap: 16px;
    row-gap: 12px;
    margin: 12px 0;
}

#carNumberWrapper > p {
    font-size: 20px;
}

.carNumber {
    display: flex;
    flex-direction: row;
    border: 2px black solid;
    border-radius: 12px;
    overflow: hidden;
    transition: 0.5s;
}

.carNumber > * {
    border: none;
}

.carNumber input {
    border: none;
    font-size: 40px;
    outline:none;
    background: none;
}
.carNumber > div {
    padding: 4px 4px;
}

.carNumber .letter {
    width: 30px;
}

.carNumber .number {
    width: 70px;
}

.carNumber > .region > input {
    width: 50px;
    font-size: 26px;
}

.carNumber > .region {
    border-left: 2px black solid;
    padding: 0px 6px;
}

.carNumber > .region img {
    height: 20px;
    border: 1px black solid;
}

.carNumber > .region > .region-flag {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    column-gap: 4px;
}

#findNamesBtn {
    padding: 4px 12px;
    font-size: 24px;
    color: #ffffff;
    background-color: #009dff;
    border-radius: 8px;
    border: none;
}

#findNamesBtn:hover {
    background-color: #1ea3ff;
    cursor: pointer;
}

.foundValue {
    background: lightgreen;
    transition: 1s;
}

.badValue {
    background: lightpink;
    transition: 1s;
}